.form-container {
  width: 700px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background: linear-gradient(
    261deg,
    rgba(245, 248, 249, 0.6278886554621849) 0%,
    rgba(237, 242, 242, 0.9724264705882353) 48%
  );
}

.form-container form {
  width: 100%;
  display: flex;
}

.label-container {
  width: 75%;
}

.top-input {
  width: 100%;
  height: 40px;
  padding: 10px;
  color: rgb(44, 42, 42);
  font-size: 20px;
  background-color: #fb00ff46;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
  border-style: none;
  outline: none;
}

.btn-submit {
  width: 25%;
  height: 40px;
  color: whitesmoke;
  font-size: 18px;
  background-image: linear-gradient(
    to right,
    #fc00ff 0%,
    #00dbde 51%,
    #fc00ff 100%
  );
  background-size: 300% auto;
  border-style: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  cursor: pointer;
  transition: 0.5s;
}

.btn-submit:hover {
  background-position: right center;
  color: #fff;
  text-decoration: none;
}

.items-container {
  margin-top: 15px;
}

.items-container h1 {
  margin-bottom: 15px;
  color: rgb(44, 42, 42);
}

.items-container input[type="text"] {
  width: 100%;
  height: 40px;
  padding: 10px;
  margin-bottom: 15px;
  border-style: solid;
  border-radius: 5px;
  border-color: rgb(117, 115, 115);
  background-color: rgba(255, 255, 255, 0);
  color: rgb(44, 42, 42);
  font-size: 20px;
  outline: none;
}

.item-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.checkbox-item {
  margin-bottom: 5px;
  cursor: pointer;
}

.item-texto {
  margin-right: 10px;
  margin-bottom: 5px;
  margin-left: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 20px;
  color: rgb(44, 42, 42);
}

.btn-remover-item {
  all: unset;
  margin-bottom: 5px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #ee64a6;
  font-size: 20px;
  cursor: pointer;
}

.btn-marcar-todos {
  width: 100%;
  height: 40px;
  padding: 5px;
  margin-top: 15px;
  border-style: none;
  border-radius: 5px;
  color: whitesmoke;
  font-size: 18px;
  background-image: linear-gradient(
    to right,
    #fc00ff 0%,
    #00dbde 51%,
    #fc00ff 100%
  );
  background-size: 300% auto;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px 0px;
  cursor: pointer;
  transition: 0.5s;
}

.btn-marcar-todos:hover {
  background-position: right center;
  color: #fff;
  text-decoration: none;
}

@media screen and (max-width: 700px) {
  .app-container {
    width: 100vw;
    padding: 0px;
  }

  .form-container {
    width: 100%;
    margin-top: 100px;
    padding: 15px;
    border-radius: 0px;
  }
}
